ul{
  position: relative;
  width:100%;
  margin: 100px auto 0;
  padding: 10px;
  box-sizing: border-box;
}

ul li{
  display: block;
  background: rgba(255,255,255,.1);
  padding: 10px 20px;
  color: #000;
  margin: 10px;
  transition: .5s; 

}

ul li:nth-child(1){
  background: rgba(255,255,255,.6);
}
ul li:nth-child(2){
  background: rgba(255,255,255,.4);
}
ul li:nth-child(3){
  background: rgba(255,255,255,.2);
}
ul li:hover{
  transform: scale(1.03);
  background:rgba(255,255,255,.6)
}

.button {
  display: inline-block;
  margin-bottom: 10px;
  margin-top:10px;
  color: #fff;
  text-transform: uppercase;
  text-decoration: none;
  position: relative;
  overflow: hidden;
  padding: 8px 25px;
  background: black;
  letter-spacing: 1px;
  border: solid thin;
}
.button:before {
  content: '';
  display: block;
  position: absolute;
  background-color: #fff;
}
.button span {
  position: relative;
  z-index: 1;
}

.button1 {
  display: inline-block;
  margin-bottom: 10px;
  margin-top:10px;
  color: #333;
  text-transform: uppercase;
  text-decoration: none;
  position: relative;
  overflow: hidden;
  padding: 8px 20px;
  background: white;
  letter-spacing: 2px;
  width: 15rem;

}

.button1:before {
  content: '';
  display: block;
  position: absolute;
  background-color: #333;
}
.button1 span {
  position: relative;
  z-index: 1;
}
.btn1 {
  transition: background 0.15s, color 0.25s;
  margin-top: 1rem;
}
.btn1:before {
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  transform-origin: center top;
  transform: scale(1, 0) translateZ(0);
  transition: all 0.25s ease-out;
}
.btn1:hover {
  color: white;
}
.btn1:hover:before {
  transform: scale(1) translateZ(0);
}

.btn2 {
  transition: all 0.4s;
}
.btn2:before {
  top: 50%;
  left: 50%;
  width: 200%;
  height: 0;
  transform: translate3d(-50%, -50%, 0) rotate(45deg);
  transition: all 0.45s;
}
.btn2:hover {
  color: black;
}
.btn2:hover:before {
  height: 400%;
}


.button:focus,.button:active{
  background:white;
  color: black;
}

