.accor {
    margin: 10px;
    transition: 0.4s; }

  .accor .tab {
    background: #272727;
    color: #fff;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    text-align: center;
    cursor: pointer;
  }
  .accor .tab span{
  margin-right: 1.5rem;
  }


  .accor .expand {
    background: #fafafa;
    padding: 0 20px;
    max-height: 0;
    overflow: hidden;
    transition: 700ms ease; 
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;

    background: #1c1c1c;
    color: #00ffb9;
  }

  .accor.active > .expand {
    background: #1c1c1c;
    color: #00ffb9;
    width: 100%;
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    border-bottom: 1px solid #00ffb9;
    border-top: 1px solid #00ffb9;

    max-height: 600px;
   }